
header {
    background-color: #1D4354;
    height: 70px;
}

header .container {
    padding: 18px 0 18px 0;
}

.nav-item {
    margin: -6px 0 0 0;
}

header .container p {
    font-size: 18px;
    color: #FFFFFF;
    font-style: normal;
    font-weight: 500;
}

.logoBlock li {
    display: inline-block;
}

.logoBlock img {
    display: none;
}

.logoBlock :nth-child(2) {
    margin-left: 10px;
}

header .container .logoBlock {
    float: left;
}

header .container .logo {
    font-weight: 500;
    font-size: 30px;
    margin-top: -8px;
}

header .container img {
    float: left;
    width: 25px;
    height: 25px;
}

header .container .logo span {
    color: #6FDA44;
}


header .row {
    width: inherit;
}

.menuBox {
    display: flex;
}

@media (max-width: 991px) {
    header {
        height: 43px;
    }
    header .container {
        padding: 4px 0 0 0;
    }
    header .container img {
        display: block;
        margin: 2px 0 0 5px;
    }
    header .container .col-6 {
        display: none;
    }
    header .container .btn-success {
        background: none;
        border: none;
        font-weight: 500;
        font-size: 16px;
        float: right;
    }
    header .container .logo {
        font-size: 25px;
        margin: -5px 0 0 35px;
    }
}

@media (max-width: 378px) {
    header .container .logo {
        font-size: 20px;
        margin: 0 0 0 35px;
    }
    header .container img {
        margin: 4px 0 0 5px;
    }
    header .container .btn-success {
        float: right;
    }
}

